import styled from 'styled-components';


const StyledNumber = styled.div`
  border-radius: 50%;
  display: flex;
  background:  ${props => props.theme.blue}
  color: ${props => props.theme.white}
`;

const Number = (props) => (
  <StyledNumber>
    {props.number}
  </StyledNumber>
);

export default Number;